@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
.sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgb(54, 9, 73);
    min-width: 5.5em;
    transition: min-width 0.5s;
    z-index: 1;
}

@media only screen and (min-height: 400px){
    .sidebar {
        position: fixed;
        height: 100vh;
    }
}

.sb-item {
    display: flex;
    width: 100%;
    height: 4.5em;
    flex-direction: row;
    align-items: center;
    text-decoration: none;
    color: whitesmoke;
}

.sb-item > span {
    opacity: 0;
    width: 0;
    overflow: hidden;
    transition: opacity 0.1s ease-in 0.05s;
    font-size: 1.5em;
    padding-left: 0.2em;
}

.sidebar:hover {
    min-width: 15em;
}

.sb-item:active > .sb-icon {
    transform: translateY(5px);
}

.sb-item:active > span {
    transform: translateY(5px);
}

.sb-item:hover {
    background-color: rgb(231, 73, 99);
}

.sidebar:hover span {
    opacity: 1;
    overflow: visible;
    overflow: initial;
}

.sb-icon {
    width: 2.5em;
    height: 2.5em;
    color: whitesmoke;
    margin-left: 1.5em;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Muli', sans-serif;
}

#root {
    min-height: 200vh;
    display: flex;
}

.content {
    background-color: rgb(99, 0, 138);
    width: 100%;
}

@media only screen and (min-height: 400px){
    .content {
        margin-left: 5.5em;
    }
}

.hidden {
    visibility: hidden;
}

::selection {
    color: rgb(231, 73, 99);
    background-color: rgb(99, 0, 138);
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type=number] {
    -moz-appearance: textfield;
}
